.image-container {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: red;
}
.image-container img {
  height: 75vh;
  border-radius: 0.4rem;
  display: block;
}
@media screen and (max-width: 33em) {
  .image-container img {
    width: 80%;
    height: 80%;
  }
}
